.admin {

	&__wrap {
		display: flex;

		@media (max-width: 1250px) {
			width: 1250px;
		}

		&.active {
			.admin__aside {
				width: 66px;
			}

			.admin__logo {
				margin-left: auto;
				margin-right: auto;

				&-1 {
					display: none;
				}

				&-2 {
					display: block;
				}
			}

			.admin__item {
				padding-left: 0;
			}

			.admin__link {
				height: 55px;
				padding-left: 0;

				&::before {
					content: none;
				}

				&::after {
					left: 50%;
					transform: translate(-50%, -50%);
				}

				&:hover {
					background-color: #424c58;
				}

				span {
					display: none;
				}

				&--active {
					background-color: #424c58;

					&.admin__link--1 {
						border-bottom: 1px solid #424c58;
						transition: none;
					}
				}
			}

			.admin__subtitle {
				display: none;
			}

			.admin__collapse {

				&::before {
					transform: rotate(180deg);
					border-radius: 0 4px 4px 0;
				}

				span {
					display: none;
				}
			}

			.admin__content {
				margin-left: 66px;
			}
		}
	}

	&__aside {
		position: fixed;
		width: 100%;
		max-width: 294px;
		height: 100vh;
		display: flex;
		flex-direction: column;
		background-color: #313C4A;
		padding: 43px 0;
		overflow-y: auto;
		z-index: 10;
	}

	&__logo {
		text-align: center;

		&-2 {
			display: none;
		}
	}

	&__title {
		font-size: 32px;
		line-height: 130%;
		color: #0A0A0A;
	}

	&__items {
		margin-top: 91px;
	}

	&__item {
		padding-left: 27px;
	}

	&__item+&__item {
		margin-top: 70px;
	}

	&__subtitle {
		margin-bottom: 5px;
		font-weight: 700;
		font-size: 10px;
		line-height: 140%;
		letter-spacing: 0.02em;
		text-transform: uppercase;
		color: #AFAFAF;
	}

	&__link {
		position: relative;
		display: block;
		padding: 17px 0 17px 38px;
		color: #fff;
		border-bottom: 1px solid #424C58;

		&:hover {
			span {
				opacity: 1;
			}

			&::before {
				right: 28px;
			}
		}

		span {
			opacity: 0.6;
			transition: all 0.3s ease;
		}

		&:last-child {
			border-bottom: none;
		}

		&::after {
			content: '';
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50%;
			left: 0px;
			transform: translateY(-50%);
			width: 21px;
			height: 21px;
		}

		&::before {
			content: '';
			background-image: url('../img/svg/arrow-right-1.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50%;
			right: 32px;
			transform: translateY(-50%);
			width: 25px;
			height: 25px;
			transition: all 0.3s ease;
		}

		&--active {
			pointer-events: none;

			&.admin__link--1 {
				border-bottom: none;
			}

			span {
				position: relative;
				opacity: 1;
			}

			&::before {
				right: 0;
				width: 100vw;
				height: 103%;
				background-color: #454D66CC;
				background-image: none;
			}
		}

		&--1 {
			&::after {
				background-image: url('../img/svg/icon-1.svg');
			}
		}

		&--2 {
			&::after {
				background-image: url('../img/svg/icon-2.svg');
			}
		}

		&--3 {
			&::after {
				background-image: url('../img/svg/icon-3.svg');
			}
		}

		&--4 {
			&::after {
				background-image: url('../img/svg/icon-4.svg');
			}
		}

		&--5 {
			&::after {
				background-image: url('../img/svg/icon-5.svg');
			}
		}

		&--6 {
			&::after {
				background-image: url('../img/svg/icon-6.svg');
			}
		}

		&--7 {
			&::after {
				background-image: url('../img/svg/icon-7.svg');
			}
		}

		&--8 {
			&::after {
				background-image: url('../img/svg/icon-8.svg');
			}
		}

		&--9 {
			&::after {
				background-image: url('../img/svg/icon-9.svg');
			}
		}

		&--10 {
			&::after {
				background-image: url('../img/svg/icon-10.svg');
			}
		}
	}

	&__collapse {
		margin-top: 50px;
		position: relative;
		text-align: center;
		color: #fff;
		opacity: 0.7;
		transition: all 0.3s ease;

		&:hover {
			opacity: 1;
		}

		&::before {
			content: '';
			background-image: url('../img/svg/arrow-left-1.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: 21px;
			position: absolute;
			top: 50%;
			right: 0;
			padding: 13px 22px;
			transform: translateY(-50%);
			background-color: #454D66CC;
			border-radius: 4px 0 0 4px;
		}
	}

	&__content {
		width: 100%;
		padding: 40px 40px 0;
		margin-left: 294px;
		height: 100vh;
		display: flex;
		flex-direction: column;
	}

	&__user {
		position: relative;

		&.active {
			background-color: #fff;
			box-shadow: 0px 5px 4px rgba(49, 65, 81, 0.1);
			border-radius: 8px 8px 0 0;
			transition: all 0.2s ease;

			.admin__user-links {
				opacity: 1;
				visibility: visible;
			}
		}

		&-header {
			display: flex;
			padding: 10px;
			cursor: pointer;
		}

		img {
			width: 30px;
			height: 30px;
		}

		&-box {
			position: relative;
			margin-left: 20px;
			padding-right: 50px;

			&::before {
				content: '';
				background-image: url('../img/svg/arrow-down-1.svg');
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
				position: absolute;
				top: 0px;
				right: 0px;
				width: 16px;
				height: 16px;
			}
		}

		&-name {
			font-weight: 700;
			font-size: 14px;
			line-height: 150%;
			color: #0A0A0A;
		}

		&-info {
			font-size: 14px;
			line-height: 140%;
			color: #AFAFAF;
		}

		&-links {
			position: absolute;
			top: 100%;
			left: 0;
			width: 100%;
			background-color: #fff;
			border-top: 1px solid #E1E5EA;
			border-radius: 0 0 8px 8px;
			box-shadow: 0 5px 4px rgba(49, 65, 81, .1);
			display: flex;
			flex-direction: column;
			padding: 15px 10px;
			opacity: 0;
			visibility: hidden;
			transition: all 0.1s ease;
		}

		&-link {
			font-size: 14px;
			font-weight: 700;

			&:hover {
				color: #F33D3D;
			}
		}

		&-link+&-link {
			margin-top: 10px;
		}
	}

	&__dropdown {
		display: flex;
		justify-content: flex-end;
	}

	&__top {
		margin-top: 40px;

		&--flex {
			display: flex;
			align-items: center;
		}

		&-item {
			display: flex;
			align-items: center;
		}

		&-item+&-item {
			margin-top: 25px;
		}
	}

	&__calendar {
		position: relative;
		padding: 0 25px;
		margin-left: 40px;
		font-weight: 700;
		font-size: 18px;
		line-height: 135%;
		color: #1F7ABC;

		&::after,
		&::before {
			content: '';
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			width: 20px;
			height: 20px;
		}

		&::before {
			background-image: url('../img/svg/arrow-left-3.svg');
			left: 0;
		}

		&::after {
			background-image: url('../img/svg/arrow-right-3.svg');
			right: 0;
		}
	}

	&__names {
		display: flex;
		margin-left: 37px;
	}

	&__name {
		display: flex;
		align-items: center;
		border: 1px solid rgba(190, 190, 190, 0.25);
		border-radius: 78px;
		padding: 8px;

		&-close {
			display: inline-flex;
			margin-left: 10px;
		}
	}

	&__name+&__name {
		margin-left: 10px;
	}


	&__add {
		background-color: #F6E81F;
		border-radius: 8px;
		padding: 11px 20px;
		font-weight: 700;
		font-size: 14px;
		line-height: 140%;
		color: #244455;
		margin-left: 15px;

		&:hover {
			background-color: #e6d812;
		}
	}

	&__form {
		display: flex;
		align-items: center;
	}

	&__form-group {
		position: relative;
		margin-left: 24px;
	}

	&__form-input {
		background: #F7F8FA;
		border: 1px solid rgba(190, 190, 190, 0.25);
		border-radius: 8px;
		width: 312px;
		height: 52px;
		outline: none;
		padding: 0 40px 0 12px;
		font-size: 16px;
		line-height: 135%;
		color: #5E5E5E;
	}

	&__form-btn {
		position: absolute;
		top: 50%;
		right: 15px;
		transform: translateY(-50%);
	}

	&__filters {
		margin-top: 28px;
		display: flex;
		align-items: center;
		padding-bottom: 15px;
		border-bottom: 1px solid #E1E5EA;
	}

	&__filter {
		position: relative;

		&:not(:last-child) {
			margin-right: 70px;
		}

		&.active {
			.admin__filter-inbox {
				opacity: 1;
				visibility: visible;
			}
		}

		&-desc {
			display: flex;
			align-items: center;
			font-weight: 700;
			font-size: 14px;
			line-height: 140%;
			color: #1F7ABC;
			cursor: pointer;
			transition: all 0.3s ease;

			&:hover {
				color: #0069b3;
			}
		}

		&-reset {
			margin-left: 5px;
		}

		&-inbox {
			opacity: 0;
			visibility: hidden;
			position: absolute;
			top: 0;
			left: 50%;
			transform: translateX(-50%);
			min-width: 270px;
			background: #fff;
			box-shadow: 0px 5px 4px rgba(49, 65, 81, 0.1);
			border-radius: 8px;
			padding: 17px 0;
			transition: all 0.2s ease;
			z-index: 1;
		}

		&-item {
			display: flex;
			align-items: center;
			justify-content: space-between;
			transition: all 0.3s ease;
			padding: 10px 16px;

			&:hover {
				background-color: #F0F1F3;
			}
		}

		&-label {
			font-weight: 700;
			font-size: 14px;
			line-height: 140%;
			color: #0A0A0A;
		}

		&-value {
			color: #E4002B;
			margin-left: 28px;
		}
	}

	&__bottom {
		padding-bottom: 30px;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	&__pagination {
		display: flex;

		&-link {
			display: flex;
			justify-content: center;
			background: #fff;
			border-radius: 4px;
			padding: 7px 14px;
			min-width: 39px;
			font-size: 17px;
			line-height: 140%;
			transition: all 0.2s ease;

			&:hover {
				background-color: #F33D3D;
				color: #fff;

				img {
					filter: brightness(10);
				}
			}

			&--active {
				background-color: #F33D3D;
				color: #fff;
			}

			&--prev {
				transform: rotate(-180deg);
			}
		}

		&-link+&-link {
			margin-left: 10px;
		}

		&-dots {
			margin: 0 10px;
			font-size: 16px;
			line-height: 135%;
			color: #0A0A0A;
			display: flex;
			align-items: flex-end;
		}
	}

	&__select {
		display: flex;
		align-items: center;

		&-desc {
			font-size: 16px;
			line-height: 135%;
			color: #A8B0BB;
			margin-right: 16px;
		}

		&-form {
			position: relative;

			&::before {
				content: '';
				background-image: url('../img/svg/arrow-down-1.svg');
				background-size: 16px;
				background-position: center;
				background-repeat: no-repeat;
				position: absolute;
				top: 50%;
				left: 15px;
				width: 100%;
				height: 16px;
				transform: translateY(-50%);
				pointer-events: none;
			}

			select {
				background-color: #fff;
				border: none;
				padding: 10px 30px 10px 10px;
				border-radius: 8px;
				appearance: none;
			}
		}
	}

	&__meet {
		background-color: #fff;
		box-shadow: 0px 1px 1px rgba(49, 65, 81, 0.1);
		border-radius: 8px;
		margin: 50px 15px 0;
		padding: 25px 20px;

		&-group {
			&--calendar {
				position: relative;

				&::before {
					content: '';
					background-image: url('../img/svg/calendar.svg');
					background-repeat: no-repeat;
					background-position: center;
					background-size: cover;
					position: absolute;
					top: 42px;
					right: 20px;
					width: 20px;
					height: 20px;
				}

				.admin__meet-input {
					padding-right: 50px;
				}
			}
		}

		&-group+&-group {
			margin-top: 25px;
		}

		&-label {
			display: block;
			font-size: 14px;
			line-height: 140%;
			color: #5E5E5E;
			margin-bottom: 8px;
		}

		&-link {
			position: relative;
			display: block;
			text-align: center;
			margin-top: 40px;
			font-weight: 700;
			font-size: 14px;
			line-height: 140%;
			color: #1F7ABC;

			&::before {
				content: '';
				background-image: url('../img/svg/arrow-left-2.svg');
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
				position: absolute;
				top: 50%;
				left: 0;
				transform: translateY(-50%);
				width: 15px;
				height: 15px;
			}
		}
	}

	&__multi {
		background-color: #313C4A;
		opacity: 0.9;
		box-shadow: 0px 4px 20px rgba(26, 29, 83, 0.05);
		border-radius: 8px;
		position: absolute;
		bottom: 65px;
		left: 50%;
		transform: translateX(-50%);
		padding: 15px 55px;
		display: flex;
		align-items: center;

		&--1 {
			width: 766px;
		}

		&--2 {
			width: 900px;
		}
	}

	&__multi-text {
		color: #fff;
	}

	&__multi-buttons {
		display: flex;
		margin: 0 35px;

		&-inner {
			display: flex;
			margin-left: 15px;
		}
	}

	&__multi-button {
		font-weight: 700;
		font-size: 14px;
		line-height: 140%;
		color: #FFFFFF;
		padding: 10px 30px;
		transition: all 0.2s ease;

		&--refuse {
			border-radius: 8px;
			background-color: #F33D3D;

			&:hover {
				background-color: #f01a1a;
			}
		}

		&--approve {
			border-radius: 8px;
			background-color: #368734;
			margin-left: 15px;

			&:hover {
				background-color: #1c8319;
			}
		}

		&--transparent {
			border: 1px solid rgba(190, 190, 190, 0.25);

			&:hover {
				background-color: rgba(190, 190, 190, 0.25);
			}

			&:first-child {
				border-radius: 8px 0px 0px 8px;
			}

			&:last-child {
				border-radius: 0px 8px 8px 0px;
			}
		}
	}

	&__multi-desc {
		font-weight: 700;
		font-size: 14px;
		line-height: 140%;
		color: #fff;
		transition: all 0.3s ease;

		&:hover {
			opacity: 0.8;
		}
	}

	&__footer {
		margin-top: auto;
		padding: 20px 0;
		border-top: 1px solid #E1E5EA;
	}
}

.table {
	position: relative;
	margin: 30px 0;

	table {
		width: 100%;
		border-collapse: collapse;
		box-shadow: 0px 1px 1px rgba(49, 65, 81, 0.1);
		border-radius: 8px;
		overflow: hidden;

		thead {
			background-color: #454D66CC;

			th {
				padding: 12px;
				text-align: left;
				color: #fff;
				font-weight: 700;
				font-size: 12px;
				line-height: 140%;
			}
		}

		tbody {
			background-color: #fff;

			tr {
				position: relative;

				&:hover {
					background-color: #F7F8FA;
				}

				&:last-child {
					td {
						border-bottom: none;
					}
				}
			}

			td {
				padding: 20px 12px;
				font-size: 14px;
				line-height: 140%;
				color: #0A0A0A;
				border-bottom: 1px solid #E1E5EA;
			}
		}
	}

	&__th {
		position: relative;
		display: inline-block;
		padding-right: 20px;
		color: #fff;
		font-weight: 700;
		font-size: 12px;
		line-height: 140%;

		&::before {
			content: '';
			background-image: url('../img/svg/filter.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50%;
			right: 0;
			transform: translateY(-50%);
			width: 10px;
			height: 10px;
		}
	}

	&__flex {
		position: relative;
		display: flex;
		align-items: center;
		z-index: 1;

		.checkbox {
			width: 16px;
			margin-right: 15px;

			span {
				max-width: 16px;
				height: 16px;
				border-radius: 4px;
			}
		}
	}

	&__status {
		padding: 8px;
		text-align: center;
		border-radius: 4px;
		font-size: 12px;
		line-height: 140%;

		&--processing {
			background-color: rgba(255, 123, 34, 0.1);
			color: #FF7B22;
		}

		&--meeting {
			background: rgba(48, 81, 159, 0.1);
			color: #30519F;
		}

		&--new {
			background: rgba(54, 135, 52, 0.1);
			color: #368734;
		}
	}

	&__date {
		margin-top: 6px;
		font-size: 12px;
		line-height: 140%;
		text-align: center;
		color: #AFAFAF;
	}

	&__user {
		position: relative;
		padding-left: 25px;
		font-weight: 500;
		font-size: 13px;
		line-height: 140%;
		color: #0A0A0A;

		&::before {
			content: '';
			background-image: url('../img/svg/account.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50%;
			left: 0;
			transform: translateY(-50%);
			width: 18px;
			height: 18px;
		}
	}

	&__value {
		position: relative;
		padding-left: 25px;
		margin-top: 5px;
		font-size: 12px;
		line-height: 140%;

		&::before {
			content: '';
			background-repeat: no-repeat;
			background-position: center;
			background-size: 12px;
			position: absolute;
			top: 50%;
			left: 0;
			transform: translateY(-50%);
			width: 18px;
			height: 13px;
		}

		&--rejected {
			color: #F33D3D;

			&::before {
				background-image: url('../img/svg/error.svg');
			}
		}

		&--meet {
			color: #30519F;

			&::before {
				background-image: url('../img/svg/communication.svg');
			}
		}

		&--created {
			color: #368734;

			&::before {
				background-image: url('../img/svg/add-one.svg');
			}
		}

		&--arrpoved {
			color: #368734;

			&::before {
				background-image: url('../img/svg/check-one.svg');
			}
		}
	}

	&__popup {
		width: 100%;
		height: 100%;
		display: block;
		position: absolute;
		top: 0;
		left: 0;
	}

	&__email {
		position: relative;
		font-size: 14px;
		line-height: 140%;
		color: #1F7ABC;
		z-index: 1;

		&:hover {
			text-decoration: underline;
		}
	}

	&__type {
		font-weight: 700;
		font-size: 14px;
		line-height: 150%;

		&--reject {
			color: #F33D3D;
		}

		&--approved {
			color: #368734;
		}
	}

	&__buttons {
		position: relative;
		z-index: 1;
	}

	&__button {
		border-radius: 8px;
		padding: 8px 20px;
		font-weight: 700;
		font-size: 14px;
		line-height: 140%;

		&--edit {
			color: #1F7ABC;
			border: 1px solid #1F7ABC;

			&:hover {
				background-color: #1F7ABC;
				color: #fff;
			}
		}

		&--delete {
			color: #E4002B;
			border: 1px solid #E4002B;

			&:hover {
				background-color: #E4002B;
				color: #fff;
			}
		}
	}

	&__button+&__button {
		margin-left: 15px;
	}

	&__location {
		position: relative;
		display: flex;
		align-items: center;

		&-text {
			padding-left: 10px;
		}
	}
}