html {
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	font-size: 16px;
	min-width: 320px;
	font-family: "GoogleSans", sans-serif;
	background-color: #F7F8FA;
}

body.lock {
	overflow: hidden;
}

input {
	border: none;

	&::placeholder {
		opacity: 1;
	}
}

textarea {
	border: none;
	resize: none;

	&::placeholder {
		opacity: 1;
	}
}

img {
	max-width: 100%;
}

a {
	text-decoration: none;
	color: #000;
	transition: all 0.3s ease;
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

button {
	border: none;
	padding: 0;
	background: transparent;
	cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
}

.input {
	width: 100%;
	background-color: #f7f8fa;
	border-radius: 8px;
	padding: 15px 12px;
	color: #0a0a0a;
	outline: 0;
}

.checkbox {
	display: flex;
	align-items: center;
	font-size: 13px;
	cursor: pointer;

	input {
		display: none;

		&:checked+span {
			background-image: url("../img/svg/checkmark.svg");
			border: none;
		}
	}

	span {
		display: block;
		background-size: contain;
		background-position: center;
		background-repeat: no-repeat;
		width: 100%;
		max-width: 24px;
		height: 25px;
		border: 2px solid #DCDCDF;
		border-radius: 7px;
	}

	i {
		font-size: 14px;
		font-style: normal;
		line-height: 140%;
		color: #0A0A0A;
		padding-left: 10px;
	}
}

.checkbox+.checkbox {
	margin-top: 12px;
}

.w-210 {
	width: 210px !important;
}

.w-180 {
	width: 180px;
}

.w-125 {
	width: 125px;
}