.popup {
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: rgba(48, 59, 73, 0.4);
	top: 0;
	left: 0;
	opacity: 0;
	visibility: hidden;
	overflow-y: auto;
	overflow-x: hidden;
	transition: all 0.3s ease 0s;
	z-index: 10;

	&.open {
		opacity: 1;
		visibility: visible;
	}

	&.open .popup__content {
		transform: none;
	}

	&__body {
		min-height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		transition: all 0.3 ease 0s;
	}

	&__content {
		background-color: #fff;
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		color: #000;
		width: 100%;
		max-width: 715px;
		position: relative;
		transition: transform .3s ease-out;
		transform: translate(0, -70px);
		border-radius: 8px;
		padding: 32px 20px;

		&--change {
			max-width: 511px;
		}

		&--request {
			max-width: 1225px;
			background-color: #F7F8FA;
		}

		&--edit {
			max-width: 470px;
		}

		&--edit-units {
			max-width: 560px;
		}

		&--delete {
			max-width: 480px;
			text-align: center;

			.popup__form-title {
				font-size: 14px;
			}

			.popup__form-desc {
				font-size: 14px;
				color: #5E5E5E;
			}
		}

		&--add {
			max-width: 780px;
			background-color: #f7f8fa;
		}
	}

	&__form {
		border-radius: 8px;

		&-top {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}

		&-title {
			font-weight: 700;
			font-size: 18px;
			line-height: 135%;
			color: #0A0A0A;

			span {
				color: #E4002B;
			}
		}

		&-desc {
			margin-top: 8px;
		}

		&-group {
			position: relative;
		}

		&-input {
			background: #f7f8fa;
			border: 1px solid rgba(190, 190, 190, .25);
			border-radius: 8px;
			width: 312px;
			height: 52px;
			outline: 0;
			padding: 0 40px 0 12px;
			font-size: 16px;
			line-height: 135%;
			color: #5e5e5e;
		}

		&-btn {
			position: absolute;
			top: 50%;
			right: 15px;
			transform: translateY(-50%);
		}

		&-categories {
			margin-top: 40px;
			display: flex;
			height: 500px;
			overflow-y: auto;
		}

		&-item+&-item {
			margin-top: 20px;
		}

		&-abc {
			margin-bottom: 10px;
			font-weight: 700;
			font-size: 12px;
			line-height: 140%;
			color: #0A0A0A;
		}

		&-buttons {
			border-top: 1px solid #E1E5EA;
			margin-top: 20px;
			padding-top: 32px;
			display: flex;
			justify-content: flex-end;

			&--change {
				border-top: none;
				margin-top: 0;

				.popup__form-button {
					width: 100%;
					font-size: 14px;
				}

				.popup__form-button--apply {
					margin-left: 15px;
				}
			}
		}

		&-button {
			transition: all 0.3s ease;
			border-radius: 8px;
			padding: 15px 35px;
			font-weight: 700;

			&--reset {
				font-size: 14px;
				line-height: 140%;
				color: #1F7ABC;
				padding: 0;

				&:hover {
					color: #0069b3;
				}
			}

			&--apply {
				margin-left: 40px;
				background-color: #E4002B;
				color: #fff;
				border: 1px solid transparent;

				&:hover {
					background-color: #fff;
					color: #E4002B;
					border-color: #E4002B;
				}
			}

			&--cancel {
				background-color: #fff;
				color: #0A0A0A;
				border: 1px solid rgba(190, 190, 190, 0.25);

				&:hover {
					background-color: #EAEDEE;
				}
			}

			&--appoint {
				margin-left: 40px;
				background-color: #F6E81F;
				border: 1px solid transparent;

				&:hover {
					background-color: #fff;
					border-color: #efe003;
				}
			}
		}

		&-password {
			margin-top: 25px;

			.popup__form-input {
				width: 100%;
			}

			.popup__form-input+.popup__form-input {
				margin-top: 8px;
			}
		}

		&-select {
			position: relative;
			width: 100%;
			margin-top: 8px;

			&::before {
				content: '';
				background-image: url('../img/svg/arrow-down-1.svg');
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
				position: absolute;
				top: 50%;
				right: 20px;
				transform: translateY(-50%);
				width: 20px;
				height: 20px;
			}

			select {
				width: 100%;
				background-color: #f7f8fa;
				border-radius: 8px;
				padding: 15px 42px 15px 12px;
				color: #0a0a0a;
				outline: 0;
				border: none;
				appearance: none;
			}
		}

		&-textarea {
			margin-top: 8px;
			background-color: #F7F8FA;
			border-radius: 8px;
			padding: 15px 12px;
			color: #0A0A0A;
			outline: none;
		}

		&-edit {
			display: grid;
			grid-gap: 20px;
			margin-top: 25px;

			.input::placeholder {
				color: #AFAFAF;
			}

			.popup__form-textarea {
				margin-top: 0;
				min-height: 117px;
			}
		}

		&-comments {
			position: relative;

			textarea {
				width: 100%;
				margin-top: 0;
				min-height: 117px;
				background-color: #f7f8fa;
				border-radius: 8px;
				padding: 15px 12px;
				outline: 0;

				&::placeholder {
					color: #AFAFAF;
				}
			}

			textarea+textarea {
				margin-top: 20px;
			}

			p {
				position: absolute;
				bottom: 20px;
				right: 20px;
				font-size: 12px;
				line-height: 140%;
				color: #5E5E5E;
			}
		}

		&-holder {
			margin-top: 32px;

			.input::placeholder {
				color: #5E5E5E;
			}
		}

		&-box {
			margin-top: 15px;
			display: flex;
			align-items: center;
			justify-content: space-between;
		}

		&-text {
			font-weight: 700;
			font-size: 18px;
			line-height: 135%;
			color: #0A0A0A;
			margin-right: 50px;
		}

		&-flex {
			position: relative;
			display: flex;
			justify-content: space-between;
			margin-top: 33px;

			.product-card__group {
				width: 100%;

				.input {
					padding-right: 50px;
				}
			}

			.product-card__group+.product-card__group {
				margin-left: 25px;
			}
		}

		&-grid {
			display: grid;
			grid-template-columns: 1fr 1fr;
			grid-gap: 30px;
			margin-top: 33px;
		}
	}

	&__top {
		display: flex;
		justify-content: space-between;
		align-items: center;
		border-bottom: 1px solid #E1E5EA;
		padding-bottom: 23px;

		&-left {
			display: flex;
			align-items: center;
		}

		&-text {
			color: #AFAFAF;
		}

		&-status {
			margin-left: 12px;
			background: #FF7B22;
			border-radius: 4px;
			padding: 4px 8px;
			font-weight: 500;
			font-size: 13px;
			line-height: 140%;
			color: #fff;
		}

	}
}

.product-card {
	margin-top: 34px;
	display: grid;
	grid-gap: 20px;

	&--2 {
		grid-template-columns: 657px 463px;
	}

	&__item {
		background-color: #fff;
		box-shadow: 0px 1px 1px rgba(49, 65, 81, 0.1);
		border-radius: 8px;
		padding: 32px 21px;

		&-next {
			padding-bottom: 0;
		}
	}

	&__item+&__item {
		margin-top: 20px;
	}

	&__subtitle {
		font-weight: 700;
		font-size: 18px;
		line-height: 135%;
		color: #0A0A0A;
	}

	&__grid {
		margin-top: 20px;
		display: grid;
		grid-gap: 20px;

		&--2 {
			grid-template-columns: repeat(2, 1fr);
		}

		&--3 {
			grid-template-columns: repeat(3, 1fr);
		}

		&--4 {
			grid-template-columns: repeat(4, 1fr);
		}
	}

	&__group {
		position: relative;
		display: flex;
		flex-direction: column;

		&--company {
			grid-column: 1/4;
		}

		&--market {
			position: relative;

			&::before {
				content: '%';
				position: absolute;
				top: 45px;
				right: 20px;
				width: 10px;
				height: 10px;
			}

			.product-card__input {
				padding-right: 35px;
			}
		}

		&--notes {
			grid-column: 1/3;
		}

		&--request {
			margin-top: 33px;
		}

		&--calendar {
			&::before {
				content: '';
				background-image: url('../img/svg/calendar.svg');
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
				position: absolute;
				top: 42px;
				right: 20px;
				width: 20px;
				height: 20px;
			}

			.input {
				padding-right: 45px;
			}
		}
	}

	&__box {
		display: flex;

		.product-card__group+.product-card__group {
			margin-left: 25px;
		}
	}

	&__label {
		font-size: 14px;
		line-height: 140%;
		color: #5E5E5E;
	}

	&__input {
		margin-top: 8px;
	}

	&__textarea {
		margin-top: 8px;
		background-color: #F7F8FA;
		border-radius: 8px;
		padding: 15px 12px;
		color: #0A0A0A;
		outline: none;
	}

	&__copy {
		position: absolute;
		top: 42px;
		right: 20px;
	}

	&__holder {
		display: flex;
		align-items: center;
		margin-top: 13px;
	}

	&__geography {
		margin-top: 20px;
		display: grid;
		grid-template-columns: repeat(3, auto);
		grid-gap: 8px;

		&-item {
			display: flex;
			align-items: center;
			font-size: 14px;
		}

		&-btn {
			display: flex;
			margin-left: 3px;
			width: 100%;
			max-width: 16px;
			height: 16px;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		&-add {
			font-weight: 700;
			font-size: 14px;
			line-height: 140%;
			color: #1F7ABC;
		}
	}

	&__look {
		border: 1px solid #E4002B;
		border-radius: 8px;
		font-weight: 700;
		font-size: 14px;
		line-height: 140%;
		color: #E4002B;
		padding: 8px 12px;

		&:hover {
			background-color: #E4002B;
			color: #fff;
		}
	}

	&__download {
		margin-left: 12px;
		font-weight: 700;
		font-size: 14px;
		line-height: 140%;
		color: #1F7ABC;

		&:hover {
			opacity: 0.7;
		}
	}

	&__flex {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__switch {
		display: flex;
		align-items: center;

		&-label {
			position: relative;
			display: inline-block;
			width: 46px;
			height: 16px;
			margin-right: 11px;

			input {
				display: none;
			}

			input:checked+.product-card__switch-slider {
				background-color: #E4002B;
			}

			input:checked+.product-card__switch-slider:before {
				transform: translateX(26px);
			}
		}

		&-slider {
			position: absolute;
			cursor: pointer;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background-color: #eaeaeb;
			transition: .4s;

			&::before {
				content: "";
				position: absolute;
				top: -4px;
				height: 24px;
				width: 24px;
				left: 0;
				bottom: 4px;
				background-color: #fff;
				transition: .4s;
				box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
			}
		}

		&-round {
			border-radius: 34px;

			&::before {
				border-radius: 50%;
			}
		}
	}

	&__img {
		width: 131px;
		height: 148px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__tools {
		text-align: center;
		margin-top: 20px;

		a+a {
			margin-left: 15px;
		}
	}

	&__input {
		&--market {
			&::placeholder {
				text-align: right;
			}
		}
	}

	&__piece {
		display: flex;
		align-items: center;

		.product-card__input {
			width: 120px;
			font-weight: 700;
			font-size: 18px;
			line-height: 135%;
			color: #0A0A0A;
		}

		p {
			margin-left: 13px;
			font-weight: 700;
			font-size: 14px;
			line-height: 150%;
			color: #5E5E5E;
		}
	}

	&__calendar {
		margin-top: 20px;
		font-weight: 700;
		font-size: 14px;
		line-height: 140%;
		color: #1F7ABC;

		span {
			position: relative;
			padding-right: 25px;

			&::before {
				content: '';
				background-image: url('../img/svg/calendar.svg');
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
				position: absolute;
				top: 50%;
				right: 0px;
				transform: translateY(-50%);
				width: 20px;
				height: 20px;
			}
		}
	}

	&__select {
		position: relative;
		margin-top: 8px;

		&::before {
			content: '';
			background-image: url('../img/svg/arrow-down-1.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			pointer-events: none;
			position: absolute;
			top: 50%;
			right: 20px;
			transform: translateY(-50%);
			width: 20px;
			height: 20px;
		}

		select {
			width: 100%;
			background-color: #f7f8fa;
			border-radius: 8px;
			padding: 15px 39px 15px 12px;
			color: #0a0a0a;
			outline: 0;
			border: none;
			appearance: none;
		}
	}

	&__inner {
		margin-top: 40px;
		padding-top: 30px;
		border-top: 1px solid #E1E5EA;
	}

	&__info {
		margin-top: 25px;
	}

	&__text {
		margin-top: 9px;
		line-height: 135%;
		color: #0A0A0A;
	}

	&__comments {
		margin-top: 20px;
	}

	&__comment {
		position: relative;
		background-color: #F7F8FA;
		padding: 15px;
		max-width: 280px;

		&::after {
			content: '';
			display: block;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 33px 0 0 33px;
			border-color: transparent transparent transparent #F7F8FA;
			position: absolute;
			top: -13px;
			left: 0;
		}

		&-name {
			font-weight: 700;
			font-size: 14px;
			line-height: 150%;
			color: #0A0A0A;
		}

		&-category {
			margin-top: 8px;
			font-size: 14px;
			line-height: 140%;
			color: #1F1F1F;
		}

		&-date {
			margin-top: 6px;
			font-size: 14px;
			line-height: 140%;
			color: #AFAFAF;
		}

		&-approved {
			display: inline-block;
			margin-top: 8px;
			background: #368734;
			border-radius: 4px;
			padding: 4px 8px;
			font-weight: 500;
			font-size: 13px;
			line-height: 140%;
			color: #FFFFFF;
		}

		&-text {
			margin-top: 6px;
			font-size: 14px;
			line-height: 140%;
		}

		&--answer {
			margin-left: auto;

			&::after {
				border-width: 0 0 33px 33px;
				border-color: transparent transparent #F7F8FA transparent;
				left: inherit;
				right: 0;
			}
		}
	}

	&__comment+&__comment {
		margin-top: 20px;
	}

	&__answers {
		margin-top: 32px;
		border-top: 1px solid #F1F1F1;

		&-input {
			margin-top: 16px;
			width: 100%;
			outline: none;
			color: #1f1f1f;

			&::placeholder {
				color: #AFAFAF;
			}
		}

		&-radios {
			margin-top: 15px;
			display: flex;
			overflow-x: auto;
			padding-bottom: 10px;
			white-space: nowrap;
		}

		&-radio {
			display: block;
			margin-right: 10px;
			border: 1px solid rgba(190, 190, 190, 0.25);
			border-radius: 8px;
			font-weight: 700;
			font-size: 14px;
			line-height: 140%;
			color: #0A0A0A;
			cursor: pointer;

			input {
				opacity: 0;
				visibility: hidden;
				position: absolute;

				&:checked+span {
					background-color: #E4002B;
					color: #fff;
				}
			}

			span {
				border-radius: 8px;
				display: block;
				width: 100%;
				height: 100%;
				padding: 8px 25px;
			}
		}
	}

	&__buttons {
		display: flex;
		margin-top: 32px;
	}

	&__button {
		border-radius: 8px;
		padding: 15px 77px;
		width: 100%;
		color: #fff;
		font-weight: 700;
		font-size: 14px;
		line-height: 140%;
		transition: all 0.3s ease;

		&--approve {
			background-color: #368734;

			&:hover {
				background-color: #1c8319;
			}
		}

		&--refuse {
			background-color: #E4002B;

			&:hover {
				background-color: #ca0026;
			}
		}

		&--disable {
			background-color: transparent;
			color: #000;
			border: 1px solid #E8E9EA;

			&:hover {
				background-color: #E3E7E9;
			}
		}

		&--delete {
			color: #E4002B;

			&:hover {
				background-color: #E3E7E9;
			}
		}

		&--transparent {
			border: 1px solid rgba(190, 190, 190, 0.25);
			color: #000;

			&:hover {
				background-color: rgba(190, 190, 190, 0.25);
			}
		}
	}

	&__button+&__button {
		margin-left: 15px;
	}

	&__sticky {
		position: sticky;
		top: 10px;
	}

	&__link {
		font-weight: 700;
		font-size: 14px;
		line-height: 140%;
		color: #1F7ABC;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		transition: all 0.3s ease;
		padding-top: 25px;

		&:hover {
			opacity: .7;
		}
	}

	&__date {
		margin-top: 35px;
		border-top: 1px solid rgba(190, 190, 190, 0.25);
		display: flex;

		.product-card__select {
			margin-top: 0;

			select {
				background-color: #fff;
				font-size: 15px;
			}
		}
	}

	&__time {
		display: flex;
		align-items: center;
		border-left: 1px solid rgba(190, 190, 190, 0.25);

		.input {
			background-color: #fff;
			padding: 0;
			margin-top: 0;
			font-size: 15px;
		}

		&-label {
			padding: 0 15px;
			font-size: 15px;
		}
	}

	&__directions {
		margin-top: 20px;
		display: flex;
		justify-content: space-between;
	}

	&__direction {
		position: relative;
		font-weight: 700;
		font-size: 14px;
		line-height: 140%;
		color: #1F7ABC;
		padding-left: 25px;

		&::before {
			content: '';
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 0px;
			left: 0px;
			width: 20px;
			height: 20px;
		}

		&--meetings {
			&::before {
				background-image: url('../img/svg/location.svg');
			}
		}

		&--timetable {
			&::before {
